import React, { Component } from 'react'
import fetchUtil from 'utils/fetchUtil'
import { getDomain } from 'utils/domain'
import { getCookie } from 'utils/tools'

class PrePay extends Component {
  buy(pageID, goods) {
    const url = getDomain('/bocxapi/v1/trade/create_order', { suffix: 'com' })
    fetchUtil.postJSON(url, {
      loginToken: getCookie('boc_loginToken'),
      siedc: 'abc',
      pageId: pageID,
      goodsInfos : JSON.stringify([{
        count: 1,
        goodsCode: goods.goodsCode,
        productId: goods.goodsSkuVOS[0].id
      }])
    }).then(data => {
      const callbackUrl = encodeURIComponent(`http://open.ffan.net/merchant/home/${pageID}`)
      window.location.href = `https://pu.ffan.com/pay/weixin/?orderNo=${data.payOrderNo}&` +
      `callBackUrl=${callbackUrl}&merchantId=${data.rainbowMerchantId}&type=5&cancelUrl=${callbackUrl}`
    }).catch(err => {
      console.log(err)
    })
  }

  render() {
    if (!this.props.goods) {
      return null
    }
    const { pageID, goods } = this.props
    return <div className="order-wrap">
      <h3 className="title">商品明细</h3>
      <div className="store-details">
        <img
          className="img"
          src={`http://timg.ffan.com/convert/resize/url_${goods.picList[0].pic}/tfs/1.png`}
        />
        <div className="store-price">
          <p className="name">{goods.name}</p>
          <p className="price-num">
            <span className="price"><i>￥</i>{goods.goodsSkuVOS[0].formatPriceCost}</span>
            <span className="num">×1</span>
          </p>
        </div>
      </div>
      <p className="store-all-price"><span>商品金额</span><span>￥{goods.goodsSkuVOS[0].formatPriceCost}</span></p>
      <p className="store-total">合计：<span>￥{goods.goodsSkuVOS[0].formatPriceCost}</span></p>
      <a
        className="pay-btn"
        onClick={() => this.buy(pageID, goods)}
      >
        立即支付<span>￥{goods.goodsSkuVOS[0].formatPriceCost}</span>
      </a>
    </div>
  }
}

export default PrePay



