import React, { Component } from 'react'

class PrePayCount extends Component {
  constructor(props) {
    super(props)
    this.state = {
      orderDate: new Date(900000 - (this.props.gmtSystem - this.props.gmtCreate)),
      overTime: this.props.gmtSystem - this.props.gmtCreate > this.props.timeLimit
    }
  }

  buy(payOrderNo, rainbowMerchantId, pageID) {
    const callbackUrl = encodeURIComponent(`http://open.ffan.net/merchant/home/${pageID}`)
    window.location.href = `https://pu.ffan.com/pay/weixin/?orderNo=${payOrderNo}&` +
      `callBackUrl=${callbackUrl}&merchantId=${rainbowMerchantId}&type=5&cancelUrl=${callbackUrl}`
  }

  componentDidMount() {
    let timer = setInterval(() => {
      this.state.orderDate = this.state.orderDate - 1000
      if (this.state.orderDate <= 0) {
        clearInterval(timer)
        timer = null
        this.setState({
          overTime: true
        })
      } else {
        this.setState({
          orderDate: new Date(this.state.orderDate)
        })
      }
    }, 1000)
  }

  render() {
    const { orderDate, overTime } = this.state
    const { orderGoodsVOS, orderNo, payOrderNo, rainbowMerchantId, pageID, createTime } = this.props
    return <div className="order-wrap">
      { !overTime
      ? <p className="status-prepaycount status-prepaycount-color">
        付款剩余时间：{orderDate.getUTCHours()}小时{orderDate.getUTCMinutes()}分{orderDate.getUTCSeconds()}秒
      </p> : null
      }
      <h3 className="title">商品明细</h3>
      <div className="store-details">
        <img
          className="img"
          src={`http://timg.ffan.com/convert/resize/url_${orderGoodsVOS[0].pic}/tfs/1.png`}
        />
        <div className="store-price">
          <p className="name">{orderGoodsVOS[0].title}</p>
          <p className="price-num">
            <span className="price"><i>￥</i>{orderGoodsVOS[0].formatPrice}</span>
            <span className="num">×1</span>
          </p>
        </div>
      </div>
      <p className="store-all-price"><span>商品金额</span><span>￥{orderGoodsVOS[0].formatPrice}</span></p>
      <p className="store-total">合计：<span>￥{orderGoodsVOS[0].formatPrice}</span></p>
      <div className="store-roder-info">
        <p>
          订单编号
          <span>
            {`${orderNo.substring(0, 4)}
              ${orderNo.substring(4, 8)}
              ${orderNo.substring(8, 12)}
              ${orderNo.substring(12, orderNo.length)}`
            }
          </span>
        </p>
        <p>下单时间<span>{createTime}</span></p>
      </div>
      {!overTime
        ? <a
          className="pay-btn"
          onClick={() => this.buy(payOrderNo, rainbowMerchantId, pageID)}
        >立即支付<span>￥{orderGoodsVOS[0].price}</span></a>
        : <a className="over-time-btn">订单已过期</a>
      }
    </div>
  }
}

export default PrePayCount



