import React, { Component } from 'react'
import { getUrlParam, getCookie } from 'utils/tools'
import fetchUtil from 'utils/fetchUtil'
import { getDomain } from 'utils/domain'
import Payed from './Payed'
import PrePay from './PrePay'
import PrePayCount from './PrePayCount'
import PayCancel from './PayCancel'

class Order extends Component {
  constructor(props) {
    super(props)

    this.state = {
      data: '',
      orderState: getUrlParam('order-state')
    }
  }
  componentDidMount() {
    document.title = '订单'
    if (getUrlParam('immediately')) {
      const url = getDomain('/bocxapi/v1/goods/goods_one', { suffix: 'com' })
      fetchUtil.getJSON(url, {
        goodsCode: getUrlParam('goodsCode')
      }).then(data => {
        this.setState({
          data: data
        })
      })
    } else {
      const url = getDomain('/bocxapi/v1/trade/order', { suffix: 'com' })
      fetchUtil.getJSON(url, {
        loginToken: getCookie('boc_loginToken'),
        orderNo: getUrlParam('orderNo'),
      }).then(data => {
        this.setState({
          data: data,
          orderState: data.orderStatus
        })
      }).catch(err => {
        console.log(err)
      })
    }
  }
  render() {
    const { pageID } = this.props.params
    let tpl
    if (getUrlParam('immediately')) {
      return <div>
        <PrePay goods={this.state.data} pageID={pageID} />
      </div>
    }
    switch (this.state.orderState) {
      case 'INITIAL':
        tpl = <PrePayCount {...this.state.data} pageID={pageID} />
        break
      case 'TRADE_FINISHED':
        tpl = <Payed {...this.state.data} pageID={pageID} />
        break
      case 'TRADE_CANCEL':
        tpl = <PayCancel {...this.state.data} />
        break
      default:
        tpl = null
    }
    return <div>
      {tpl}
    </div>
  }
}

export default Order



